import { Component, AfterViewInit } from '@angular/core';
import { Noise } from './noise';

@Component({
  selector: 'app-root',
  template: `
  <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 10 10" (click)="perform()">
    <rect *ngIf="play; else elseBlock" fill="#000000" x="2" y="2" width="6" height="6"></rect>  
    <ng-template #elseBlock>
      <path fill="#000000" d="M 2,2 L 8,5 2,8 Z"></path>
    </ng-template>
  </svg>
  `,
  styles: []
})
export class AppComponent implements AfterViewInit {
  title = 'White Noise';

  play = false
  noise: Noise;

  ngAfterViewInit(){
    this.noise = new Noise()
  }

  perform() {
    if (this.play) {
      this.noise.stopNoise(Noise.template)
    } else {
      this.noise.playNoise(Noise.template)
    }
    this.play = !this.play;
  }
}
